body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Poppins", Sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(0, 63, 158);
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top {
 
  text-align: center;
  background-color: rgb(255, 163, 25);
 
}

.h1, h1{
  text-align: center;
 font-size: calc(8px + 1vmin);
color: azure;
padding: 1.2em;
margin-bottom: 0px;
}

.h2, h2{
  text-align: center;
font-size: calc(15px + 1vmin);
font-weight: bold;
color: azure;
padding-top: 20px;
padding-bottom: 0px;
padding-left: 40px;
padding-right: 40px;
margin-bottom: -5px;
}


.h3, h3{
  text-align: center;
font-size: calc(8px + 2vmin);
color: azure;
padding-left: 40px;
padding-right: 40px;
padding-top: 10px;
padding-bottom: 10px;
}

.h4, h4{
  text-align: center;
font-size: calc(25px + 2vmin);
color: azure;
padding-top: 10px;
}

.h5, h5{
  text-align: center;
font-size: calc(8px + 1.2vmin);
color: azure;
padding-left: 40px;
padding-right: 40px;
padding-top: 10px;

}

.p {
  color: aliceblue;
}

.img1 {
  padding-right: 20px;
  padding-left: 20px;
}


.zap {
  padding-bottom: 15px;
  
}

.mid {
  background-color: black;
  padding-bottom: 10px;
}

.prog {
  padding-right: 60px;
  padding-left: 60px;
  min-width: 80%;
  min-height: 80%;
  padding-top: 20px;
  padding-bottom: 15px;
}

.w-50 {
  width: 100%!important;
}

.m-5 {
  
  margin-top: 0rem !important;
  margin-right: 0rem !important;
  margin-bottom: 1rem !important;
  margin-left: 0rem !important;
}

.progress-bar {
  
  color: #000;
  padding-top: 12px;

}

.img-fluid {
  max-width: 80%;
}

#rectangle{
  width:70%;
  height:4px;
  background:rgb(255, 230, 0);
  display: inline-block;
  padding-left: 100px;
}